/*
*
* MetaBoxes
*
*/
#cpt_vi_metabox_1 .cpt_vi_metabox1 {
  display: flex;
  gap: 10px;
  flex-wrap: wrap;
  flex-direction: column;
  justify-content: flex-start;
}
@media screen and (min-width: 1024px) {
  #cpt_vi_metabox_1 .cpt_vi_metabox1 {
    flex-direction: row;
    align-self: center;
    align-items: center;
    align-content: center;
    gap: 30px;
  }
}
#cpt_vi_metabox_1 .cpt_vi_metabox1 p {
  display: flex;
  align-items: center;
  gap: 14px;
}
@media screen and (min-width: 1024px) {
  #cpt_vi_metabox_1 .cpt_vi_metabox1 p {
    flex: 1 1 calc(50% - 30px);
    max-width: calc(50% - 15px);
  }
}
#cpt_vi_metabox_1 .cpt_vi_metabox1 p label {
  width: 120px;
  display: flex;
  font-weight: 500;
  line-height: 1.2;
}
#cpt_vi_metabox_1 .cpt_vi_metabox1 p input {
  width: 100%;
}
#cpt_vi_metabox_1 .tabs-panel {
  height: 80px !important;
}